import React from 'react';
import { Icon } from 'react-icons-kit';
import { ic_insert_photo } from 'react-icons-kit/md';

const MoviePageList = ({
  rank,
  rankInten,
  rankOldAndNew,
  movieCd,
  movieNm,
  openDt,
  salesAmt,
  salesInten,
  salesChange,
  salesAcc,
  audiCnt,
  audiInten,
  audiChange,
  audiAcc,
  scrnCnt,
  showCnt,
  photo
}) => {
  return (
    <li>
      <div className="poa">
        <div className="poster">
          {!photo ? (
            <Icon icon={ic_insert_photo} title="" />
          ) : (
            <img src={require(`../../images/movie/${movieCd}.jpg`)} alt="" />
          )}
        </div>
        <i className="rank">{rank}</i>
        <i className="rankInten">
          {(() => {
            if (rankOldAndNew === 'NEW') {
              return <span className="new">NEW</span>;
            } else {
              if (`${rankInten}` > 0) {
                return (
                  <>
                    <span className="blue"></span> {rankInten}
                  </>
                );
              } else if (`${rankInten}` < 0) {
                return (
                  <>
                    <span className="red"></span> {rankInten.replace(/-/g, '')}
                  </>
                );
              } else {
                return <span className="maintain">-</span>;
              }
            }
          })()}
        </i>
      </div>
      <p className="title">{movieNm}</p>
      <p className="release">
        : {openDt} ({movieCd})
      </p>
      <ul>
        <li>
          <span>:</span>{' '}
          {scrnCnt.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span> :</span>{' '}
          {showCnt.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span>:</span>{' '}
          {audiCnt.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span> :</span>{' '}
          {audiAcc.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span>  :</span>{' '}
          {audiInten.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span>   :</span> {audiChange}%
        </li>
        <li>
          <span>:</span>{' '}
          {salesAmt.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span> :</span>{' '}
          {salesAcc.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span>   :</span>{' '}
          {salesInten.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')}
        </li>
        <li>
          <span>    :</span> {salesChange}%
        </li>
      </ul>
    </li>
  );
};

export default MoviePageList;
