import React from 'react';
import './Header.scss';
import { Icon } from 'react-icons-kit';
import {
  ic_alarm,
  ic_alarm_add,
  ic_alarm_off,
  ic_alarm_on
} from 'react-icons-kit/md';

const Header = () => {
  return (
    <header className="header">
      <h1>  </h1>
      <p>      .</p>
      <div className="icons">
        <Icon icon={ic_alarm} />
        <Icon icon={ic_alarm_add} />
        <Icon icon={ic_alarm_off} />
        <Icon icon={ic_alarm_on} />
      </div>
    </header>
  );
};

export default Header;
