import React from 'react';
import { Link } from 'react-router-dom';

const MovieSearchPageList = ({
  movieNm,
  movieNmEn,
  prdtYear,
  openDt,
  typeNm,
  prdtStatNm,
  nationAlt,
  genreAlt,
  directors,
  companys,
  onMovieInAnchor
}) => {
  const directorsPeopleNm = directors.map((info, index) => {
    return (
      <Link
        to={{
          pathname: ''
        }}
        key={index}
        onClick={onMovieInAnchor}
      >
        {info.peopleNm}
      </Link>
    );
  });
  return (
    <li>
      <ul>
        <li>
          <span>():</span> {movieNm}
        </li>
        <li>
          <span>():</span> {movieNmEn}
        </li>
        <li>
          <span>:</span> {prdtYear}
        </li>
        <li>
          <span>:</span>{' '}
          {!openDt
            ? ''
            : openDt.substring(0, 4) +
              ' ' +
              openDt.substring(4, 6) +
              ' ' +
              openDt.substring(6, 8) +
              ''}
        </li>
        <li>
          <span>:</span> {typeNm}
        </li>
        <li>
          <span>:</span> {prdtStatNm}
        </li>
        <li>
          <span>:</span> {nationAlt}
        </li>
        <li>
          <span>:</span> {genreAlt}
        </li>
        <li>
          <span>:</span> {directorsPeopleNm}
        </li>
        <li>
          <span>:</span> {companys.join(', ')}
        </li>
      </ul>
    </li>
  );
};

export default MovieSearchPageList;
